




























import Component from 'vue-class-component';
import Vue from 'vue';
import { Prop } from 'vue-property-decorator';

@Component({})
export default class Modal extends Vue {
  @Prop() isVisible!: boolean;

  emitCancel() {
    this.$emit('cancel');
  }

  emitConfirm() {
    this.$emit('confirm');
  }
}
